<template>
  <el-row style="margin-top: 60px;">
    <el-col :span="6" :offset="6">
      <el-form>
        <el-form-item label="用户名">
          <el-input v-model="username" placeholder="用户名"></el-input>
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="password" placeholder="密码"></el-input>
        </el-form-item>
        <el-form-item>
          <el-checkbox v-model="rememberMe">记住我</el-checkbox>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="login">登录</el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>

<script>
import Vue from 'vue'
export default {
  name: 'Login',
  data () {
    return {
      username: '',
      password: '',
      rememberMe: true
    }
  },
  methods: {
    login () {
      this.$http.post('http://localhost:8080/api/authenticate', {username: this.username, password: this.password, rememberMe: this.rememberMe}).then(function (res) {
        this.$cookie.set('Authorization', 'Bearer ' + res.data.id_token)
        this.$router.push('/')
      }, function (err) {
        console.log(err)
      })
    },
    onSubmit () {
      console.log('submit')
      // Vue.http.headers.common['Authorization'] = 'Basic YXBpOnBhc3N3b3Jk'
      console.log(Vue.http.headers)
    }
  }
}
</script>

<style scoped>

</style>



