<template>
  <div class="dp-container">
    <h1>{{errorData.code}}</h1>
    <h2>{{errorData.message}}</h2>
    <el-button @click="goHome">返回首页</el-button>
    <el-button @click="goBack">返回上一页</el-button>
  </div>
</template>

<script>
export default {
  name: 'Unauthorized',
  data () {
    return {
      errorData: this.$route.params
    }
  },
  created () {
    console.log(this.$route.params)
  },
  methods: {
    goHome () {
      this.$router.push('/')
    },
    goBack () {
      this.$router.go(-2)
    }
  }
}
</script>

<style scoped>

</style>



