<template>
  <div class="dp-container">
    <h1>404</h1>
    <h2>找不到页面</h2>
    <el-button @click="goHome">返回首页</el-button>
    <el-button @click="goBack">返回上一页</el-button>
  </div>
</template>

<script>
export default {
  name: 'NotFoundComponent',
  methods: {
    goHome () {
      this.$router.push('/')
    },
    goBack () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped>

</style>



