<template>
  <el-container>
    <el-header>
      <div class="el-aside_logo">
        LOGO
      </div>
      <div>
        <el-button size="small" @click="logout">评论</el-button>
        <img src="../assets/logo.png" width="20" alt="">
      </div>
    </el-header>

    <el-container>
      <el-aside width="200px">
        <el-row class="tac">
          <el-col :span="24">
            <div class="user-details">
              <img src="../assets/logo.png">
              <div class="user-info">
                <h4 class="font-16 text-white">Elena Retson</h4>
                <span class="text-white"><i class="fa fa-dot-circle-o text-success "></i> Online</span>
              </div>
            </div>
            <el-menu
              router="router"
              default-active="2"
              background-color="#404E67"
              text-color="#fff"
              active-text-color="#0f9cf3"
              @open="handleOpen"
              @close="handleClose">
              <el-submenu index="1">
                <template slot="title">
                  <i class="el-icon-location"></i>
                  <span>导航一</span>
                </template>
                <el-submenu index="1-4">
                  <template slot="title">选项4</template>
                  <el-menu-item index="1-4-1">选项1</el-menu-item>
                </el-submenu>
              </el-submenu>
              <el-menu-item index="/login">
                <i class="el-icon-menu"></i>
                <span slot="title">导航二</span>
              </el-menu-item>
              <el-menu-item index="/t">
                <i class="el-icon-setting"></i>
                <span slot="title" route="/table" index="/table">导航四</span>
              </el-menu-item>
            </el-menu>
          </el-col>
        </el-row>
      </el-aside>
      <el-container>
        <el-main><router-view/></el-main>
        <el-footer>Footer</el-footer>
      </el-container>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your  Vue.js App'
    }
  },
  methods: {
    handleOpen (key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose (key, keyPath) {
      console.log(key, keyPath)
    },
    logout () {
      this.$cookie.delete('Authorization')
      this.$router.push('/login')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
  .el-container {
    height: 100%;
  }
  .el-menu-item, .el-submenu {
    width: 200px;
    margin: 0px;
  }
</style>



