<template>
  <router-view/>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      router: true
    }
  },
  methods: {
    handleOpen (key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose (key, keyPath) {
      console.log(key, keyPath)
    },
    logout () {
      this.$cookie.delete('Authorization')
      this.$router.push('/login')
    }
  }
}
</script>

<style>
html, body {
  height: 100%;
  margin: 0px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
}
.el-header, .el-footer {
  background-color: #fff;
  color: #333;
  line-height: 60px;
  box-shadow: 1px 0px 20px rgba(0, 0, 0, 0.05);
  padding: 0px !important;
}

.tr {
  text-align: right;
}
.tl {
  text-align: left;
}
.tc {
  text-align: center;
}

.el-aside {
  background-color: #404E67;
  color: white;
  text-align: center;
  /*line-height: 200px;*/
}
.el-menu {
  text-align: left;
}
.el-menu-item.is-active {
  background-color: #38445A!important;
}

.el-main {
  background-color: #E9EEF3;
  color: #333;
  text-align: center;
  /*line-height: 160px;*/
}
.item {
  margin-top: 10px;
  margin-right: 40px;
}

.el-submenu__title{

}
.el-aside_logo {
  line-height: 60px;
  box-shadow: 2px 4px 20px -4px rgba(0, 0, 0, 0.1);
  background-color: #404E67;
  width: 200px;
  text-align: center;
  color: #fff;
  float: left;
}
.user-details {
  min-height: 80px;
  padding: 20px;
  position: relative;
}
.user-details img {
  position: relative;
  z-index: 9999;
  height: 64px;
  width: 64px;
}
/*滚动条样式*/
::-webkit-scrollbar{
  background: rgb(158, 165, 171);
  width: 3px;
  position: absolute;
  top: 122px;
  opacity: 0.4;
  display: block;
  border-radius: 7px;
  z-index: 99;
  right: 1px;
}

/*定义滑块 内阴影+圆角*/
::-webkit-scrollbar-thumb
{
  border-radius: 7px;
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
  background-color: #3f4e67;
}
.dp-container {
  text-align: center;
}
</style>



